*{
    padding: 0;
    margin: 0;
}


#movie_list{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    box-sizing: border-box;
}

#movie{
    padding-left: 123px;
    flex-basis: 25%;
    margin-bottom: 30px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.467);
    padding-bottom: 30px;
}

li{
    margin-left: 13px;
    list-style: '- ';
}

#summary{
    width: 230px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3; /* 표시하고자 하는 라인 수 */
    -webkit-box-orient: vertical;
}


h2{
    width: 350px;
    margin-top: 5px;
    margin-bottom: 10px;
}

h4{
    margin-top: 10px;
}

img{
    margin-top: 15px;
}

a{
    text-decoration: none;
    color: inherit;
}
h1{
    width: 100%;
    text-align: center;
    padding: 30px 0px;
}

#link{
    border:none;
    border-radius: 10px;
    color: white;
    background-color: tomato;
    padding: 6px 12px;
    position: absolute;
    top: 78%;
    left: 59%;
    transform: translate(-59%,-78%);
}

#detail{
    height: 97vh;
    width: 100%;
    left: 50%;
    top: 50%;
    position: absolute;
    transform: translate(-50%,-50%);
    box-sizing: border-box;
    padding: 50px 250px;
}

#description{
    display: flex;
    height: 500px;
    width: 100%;
    margin-top: 20px;
    padding-top: 10px;
    background-color: white;
}
#title{
    width: 100%;
    padding-left: 10px;
    font-size: 35px;
}

#detail_des{
    margin-bottom: 5px;
    margin-top: -5px;
    width: 600px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 10; /* 표시하고자 하는 라인 수 */
    -webkit-box-orient: vertical;
}

#mini1{
    display: inline-block;
    height: 500px;
    margin-right: 10px;
    padding: 20px;
    box-sizing: border-box;
}

#mini1>span{
    margin-bottom: 10px;
    display: inline-block;
    color: rgba(0, 0, 0, 0.637);
    margin-top: 5px;
}

#mini1>h4{
    display: inline;
}

#des_img{
    display: inline;
    height: 400px;

    margin-left: 45px;
    margin-top: 30px;
}

#container{
    background-color: rgba(0, 0, 0, 0.350);
    padding: 13px;
    border-radius: 5px;
}

#loading{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}